@import url(https://fonts.googleapis.com/css?family=Raleway:400,700|EB+Garamond|Playfair+Display|Fira+Sans+Condensed|Roboto:400,500,600);
html, body, #root {
  max-height: 100vh;
  max-width: 100vw;
}

html, body {
    overflow: hidden;
}

#root {
    overflow-x: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
}

* {
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  background-color: #E0E0E0;
}

/*** Navigation Bar **/
#navigation-bar {
    background-color: rgba(0, 0, 0, 0.9);
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    box-shadow: none;

    position: fixed;
    top: 0;
    z-index: 100;
    width: 100%;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;

    transition:
        background-color 0.15s ease-in-out;
}

html[data-scroll='0'] #navigation-bar {
    -webkit-backdrop-filter: none;
            backdrop-filter: none;
    background-color: transparent;
}

#main-bar {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;

    height: 56px;
}


/*** Logo ***/
#main-logo-container {
    display: block;

    margin-left: 25px;
    margin-right: 45px;
}

#main-logo {
    font-family: 'Fira Sans Condensed', sans-serif;
    color: white;

    font-size: 30px;
    line-height: 1;
    text-decoration: none;

    transition: color 0.2s ease-in-out;
}

#main-logo:hover {
    color: #ddd;
}


/*** Tabs ***/
.navigation-tabs {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.nav-tab {
    -webkit-flex-grow: 1;
            flex-grow: 1;

    display: -webkit-flex;

    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;

    color: white;
    font-family: 'Raleway';
    font-size: 18px;

    transition: background-color 0.15s ease-in-out;
}

html:not([data-scroll='0']) .nav-tab.selected {
    background-color: rgba(100, 100, 100, 0.5);
}

html:not([data-scroll='0']) .nav-tab:hover:not(.selected) {
    background-color: rgba(255, 255, 255, 0.05);
}

.desktop.navigation-tabs {
    width: 250px;
    height: 100%;
}

.mobile.navigation-tabs {
    display: none;
}


/*** Social Icons ***/
#social-buttons {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;

    position: absolute;
    right: 10px;

}

#social-buttons > a {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
}

.social-icon {
    width: 30px;
    margin-right: 20px;
}


/***** Media Queries *****/
@media (max-width: 540px) {
    .mobile.navigation-tabs {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: row;
                flex-direction: row;
        height: 40px;
    }

    html[data-scroll='0'] .mobile.navigation-tabs {
        border-bottom: 1px solid rgba(255, 255, 255, 0.3);
    }

    .desktop.navigation-tabs {
        display: none;
    }

    #main-bar {
        height: 44px;
    }

    #main-logo-container {
        margin-left: 10px;
    }

    #main-logo {
        font-size: 24px;
    }

    #social-buttons {
        right: 0;
    }

    .social-icon {
        width: 25px;
        margin-right: 10px;
    }
}

.banner-container {
    left: 0;
    right: 0;

    position: relative;
    height: 450px;
}

html {
    -webkit-perspective: 1000px;
            perspective: 1000px;
    -webkit-perspective-origin: center 225px;
            perspective-origin: center 225px;
}

.banner-background {
    width: 100%;
    height: 100%;

    /* Background Image */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    /* Parallax Scroll */
    z-index: -100;
    -webkit-transform: translateZ(-1000px) scale(2.1);
            transform: translateZ(-1000px) scale(2.1);
}

.banner-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding-top: 20px;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    background: linear-gradient(
        rgba(0, 0, 0, 0.3),
        rgba(0, 0, 0, 0.6)
    );
}

.banner-text {
    display: block;
    position: relative;

    margin: 0 40px;

    color: white;
    text-align: center;
}

.banner-text > h1 {
    font-family: 'Raleway', sans-serif;
    font-weight: 700;
    font-size: 90px;
    margin: 0;
}

.banner-text > p {
    font-family: 'Raleway';
    font-size: 28px;
    line-height: 1.4;
    margin-top: 0.8em;
}

.down-arrow {
    position: absolute;
    bottom: 30px;
    display: none;
}

.down-arrow img {
    width: 50px;
}

@-webkit-keyframes down-arrow {
    from {bottom: 30px;}
    to {bottom: 20px;}
}

@keyframes down-arrow {
    from {bottom: 30px;}
    to {bottom: 20px;}
}


/* Media Queries */

@media (max-width: 600px) {
    .banner-container {
        height: 350px;
    }

    .banner-overlay {
        padding-top: 80px;
    }

    html {
        -webkit-perspective-origin: center 175px;
                perspective-origin: center 175px;
    }

    .banner-text > h1 {
        font-size: 54px;
    }

    .banner-text > p {
        font-size: 20px;
    }
}

@media (max-height: 600px) {
    .banner-container {
        height: 100vh;
    }

    html {
        -webkit-perspective-origin: center;
                perspective-origin: center;
    }

    .down-arrow {
        display: block;

        -webkit-animation-name: down-arrow;

                animation-name: down-arrow;
        -webkit-animation-direction: alternate;
                animation-direction: alternate;
        -webkit-animation-duration: 1s;
                animation-duration: 1s;
        -webkit-animation-iteration-count: infinite;
                animation-iteration-count: infinite;
        -webkit-animation-timing-function: ease-in-out;
                animation-timing-function: ease-in-out;
    }
}

#content-container {
  background: #E0E0E0;
  z-index: 1000;
  position: relative;

  padding: 40px;
}

.text-container {
  left: 0;
  right: 0;

  width: 600px;
  max-width: 85vw;
  margin: 0 auto;
}

.text-container > p {
  text-align: justify;

  margin-top: 0;
  font-family: 'EB Garamond', serif;
  font-size: 22px;
}


/* Media queries */
@media (max-width: 1000px) {
  #content-container {
    padding: 30px;
  }
}

@media (max-width: 540px) {
  #content-container {
    padding: 15px;
  }

  .text-container {
    margin-top: 15px;
  }

  .text-container > p {
    text-align: left;
    font-size: 18px;
  }
}

#project-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  grid-gap: 15px;
}

@media (max-width: 400px) {
    #project-container {
        grid-template-columns: 1fr;
    }
}

/***** Project Cards *****/
.project-card {
        place-self: stretch stretch;

	overflow: hidden;

	display: -webkit-flex;

	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	cursor: pointer;

	transition: all 0.2s ease-in-out;
}

.project-card:hover {
	-webkit-transform: scale(1.03);
	        transform: scale(1.03);
	box-shadow: 5px 5px 10px rgba(0,0,0,0.3);
}

.project-info-container {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-flex-grow: 1;
	        flex-grow: 1;
}

.project-image {
	height: 200px;
}


/*** Header ***/
.project-header {
	display: -webkit-flex;
	display: flex;
	width: 100%;
}

.project-title {
	float: left;
	overflow-x: hidden;
	-webkit-flex-grow: 1;
	        flex-grow: 1;
}

.project-date {
	float: right;
	text-align: right;
	padding-top: 3px;
}

/*** Content ***/
.project-info {
	-webkit-flex-grow: 1;
	        flex-grow: 1;
}


.project-info p {
        margin-top: 10px;
}

a {
	text-decoration: none;
	color: black;
}

/*** Footer ***/
.project-footer {
	margin-top: auto;
	display: -webkit-flex;
	display: flex;
}

/* Tags */
.project-tags-container {
	/* Fill all allotted space */
	display: -webkit-flex;
	display: flex;
	-webkit-flex-grow: 1;
	        flex-grow: 1;

	/* Positioning and element wrapping*/
	white-space: nowrap;
	margin-right: 10px;
	margin-top: 3px;

	/* Clip scrollbar in .project-tags */
	overflow: hidden;
	height: 32px;
}

.project-tag-label {
	padding-top: 9px;
}

.project-tags {
	display: inline-block;
	white-space: nowrap;
	overflow-x: auto;

	height: 60px; /* Push scrollbar out of view */
}

.project-tag {
	margin-left: 6px;
}

/* Social Icons */
.project-social {
	display: inline-block;
	float: right;
	white-space: nowrap;
}

.project-social a {
	padding: 4px;
}

.project-social a img {
	width: 32px;
}


/***** Media Queries *****/
@media (max-width: 480px) {
	.project-header {
		display: inline-block;
	}
	
	.project-title {
		width: 100%;
	}

	.project-date {
		float: left;
		text-align: left;
		margin-top: 2px;
	}
}

.tag-filter {
    padding: 8px 15px;
    margin-bottom: 15px;
    height: 32px;

    position: -webkit-sticky;

    position: sticky;
    top: 65px;
    z-index: 10;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    overflow: hidden;

    transition:
        height 0.2s ease-in-out,
        padding 0.2s ease-in-out,
        margin 0.2s ease-in-out;
}

.tag-filter .tags {
    overflow-x: auto;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    -webkit-flex-basis: 0px;
            flex-basis: 0px;
    white-space: nowrap;
}

.tag-filter.empty {
    height: 0px;

    padding-top: 0;
    padding-bottom: 0;

    margin-bottom: 0;
}

.tag-filter p {
    margin: 0;
    margin-right: 5px;

    color: #555;
    font-family: Roboto, sans-serif;
    font-size: 14px;
}

@media (max-width: 540px) {
    .tag-filter {
        top: 100px;
    }
}



