@import url('https://fonts.googleapis.com/css?family=Raleway:400,700|EB+Garamond|Playfair+Display|Fira+Sans+Condensed|Roboto:400,500,600');

html, body, #root {
  max-height: 100vh;
  max-width: 100vw;
}

html, body {
    overflow: hidden;
}

#root {
    overflow-x: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
}

* {
    transform-style: preserve-3d;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  background-color: #E0E0E0;
}
