.banner-container {
    left: 0;
    right: 0;

    position: relative;
    height: 450px;
}

html {
    perspective: 1000px;
    perspective-origin: center 225px;
}

.banner-background {
    width: 100%;
    height: 100%;

    /* Background Image */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    /* Parallax Scroll */
    z-index: -100;
    transform: translateZ(-1000px) scale(2.1);
}

.banner-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding-top: 20px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    background: linear-gradient(
        rgba(0, 0, 0, 0.3),
        rgba(0, 0, 0, 0.6)
    );
}

.banner-text {
    display: block;
    position: relative;

    margin: 0 40px;

    color: white;
    text-align: center;
}

.banner-text > h1 {
    font-family: 'Raleway', sans-serif;
    font-weight: 700;
    font-size: 90px;
    margin: 0;
}

.banner-text > p {
    font-family: 'Raleway';
    font-size: 28px;
    line-height: 1.4;
    margin-top: 0.8em;
}

.down-arrow {
    position: absolute;
    bottom: 30px;
    display: none;
}

.down-arrow img {
    width: 50px;
}

@keyframes down-arrow {
    from {bottom: 30px;}
    to {bottom: 20px;}
}


/* Media Queries */

@media (max-width: 600px) {
    .banner-container {
        height: 350px;
    }

    .banner-overlay {
        padding-top: 80px;
    }

    html {
        perspective-origin: center 175px;
    }

    .banner-text > h1 {
        font-size: 54px;
    }

    .banner-text > p {
        font-size: 20px;
    }
}

@media (max-height: 600px) {
    .banner-container {
        height: 100vh;
    }

    html {
        perspective-origin: center;
    }

    .down-arrow {
        display: block;

        animation-name: down-arrow;
        animation-direction: alternate;
        animation-duration: 1s;
        animation-iteration-count: infinite;
        animation-timing-function: ease-in-out;
    }
}
